.flat{
    &:hover{
        .flat-path{
            fill: #70ff1963;
            stroke: #62ff00;
        }
    }
    &.active{
         .flat-path{
           fill: #05ff0087;
           stroke: #05FF00;
}
    }
    
    &-path{
        fill: #ff000000;
        stroke-width: 5px;
}    
    &.action{
        .flat-status-cell__path{
        fill:#80D34E;
    }
}

    &.booking{
        .flat-status-cell__path{
        fill:#6868ff;
    }
}

    &.already-sold{
        .flat-status-cell__path{
        fill:#d34e4e ;
    }
} 
    
}


.flat-status-path{
    fill: white;
    stroke: black;
    stroke-width: 2px;
}

.flat-status-cell__path{
    fill:#00defc; 
    stroke:black;
    stroke-width:2px;
}

.flat-status-cell__text{
    fill: black;
    font-weight: 600;
}

.floor-options {
    @extend .container;
    display: flex;
    gap: 24px;
    padding: 24px;
    border: 1px solid;
    margin: 12px auto;
    background: $bg-primary-color;
    color: #fff;
    border-radius: 8px;
    
}

.floor-option-item {
    width: 100%;
    text-align: center;
    background-color: $bg-accent;
    padding: 11px;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
}
