header{
	height: 11vh;
	padding: 15px 0;
	background: $bg-primary-color;

	.container{
		display: inline-flex;
	}
	
	a {
		color: #fff;
		font-size: 20px;
		text-decoration: none;

		&:hover{
			color: orange;
		}
		&.logo{
		max-width: 200px;
	    }
	}
}

.header-navigation{
	display: flex;
    gap: 20px;
    align-items: flex-end;
    margin-left: 48px;
}