.plan{
    position: relative;
}

.item-path {
    fill: rgb(18 255 0 / 57%);
    stroke: #3aff04;
    stroke-width: 3px;
    fill-opacity: 0;
    stroke-opacity: 0;
    &:hover{
    fill-opacity: .4;
    stroke-opacity:2;
    }
}

.template-navigation {
    position: absolute;
    bottom: 100px;
    right: 100px;
    background: #fff;
    padding: 28px;
    border-radius: 15px;

&-title{
    margin-bottom: 12px;
}
}

.information-list{
    list-style: none;
}

.sold{
    fill: #ec4848;
    stroke: #6d0000;
}
